import React from 'react'
import Helmet from 'react-helmet'
import styled from 'styled-components'
import Wrapper from '../../../components/wrapper'
import {
  Row,
  Col,
  Container,
} from 'reactstrap'

const container = styled.div`
  padding: 3rem;
`

const chat_container = styled.div`
  background-color: #f5f6fa;
  padding: 1rem;
  margin: 1rem 0;
  box-shadow: 0 10px 6px -6px #777;
`

const IndexPage = ({ pathContext }) => {
  const { group, index, pageCount } = pathContext

  return (
    <Wrapper>
      <Helmet
        title='Saved Messages'
        meta={[
          { name: 'description', content: 'Kirim pesan untuk dibaca di komputer lain' },
        ]}
      />
      <Row>
      <Col lg="5">
        <h2 className="text-center">Simpan Pesan</h2>
      </Col>
      <Col lg="7">
        <h2 className="text-center">Pesan Tersimpan</h2>
      </Col>
      </Row>
    </Wrapper>
  )
}
export default IndexPage


